<template>
  <div>
    <div class="circle" @click="start">{{ waiting }}</div>
    <input type="file" accept="video/*" capture="camcorder" ref="camera" @change="handlerUpload($event)" style="display:none">
    <p v-if="result!==''">上传结果显示<br>{{passed[result] +'：' + status[liveness_status]}}</p>
  </div>
</template>

<script>
import $ from 'webpack-zepto'
export default {
  name: 'HelloWorld',
  data () {
    return {
      Authorization: '',
      formdata: null,
      status: {
        'ok': '真人',
        'hack': '假造人脸',
        'short_time': '视频时长不足2s',
        'no_face_detected': '视频中未检测到人脸',
        'loss_tracking': '人脸中途出框',
        'face_changed': '视频中途人脸被替换'
      },
      liveness_status: '',
      passed: {
        true: '通过',
        false: '不通过'
      },
      result: '',
      waiting: '开始识别人脸',
      isstart: false
    }
  },
  mounted () {
    window.$ = $
    this.getAuth()
  },
  methods: {
    start () {
      if (!this.isstart) {
        this.$refs.camera.value = ''
        this.$refs.camera.click()
        this.isstart = true
      }
    },
    getAuth () {
      $.ajax({
        url: 'http://13.231.214.207/encry.php',
        type: 'get',
        success: (res) => {
          this.Authorization = JSON.parse(res).result
        }
      })
    },
    setwaitingwz () {
      this.waiting = '正在等待上传'
      this.result = ''
      let times = 0
      this.setInterval = setInterval(() => {
        if (times === 3) {
          times = 0
          this.waiting = '正在等待上传'
        } else {
          this.waiting += '..'
          times++
        }
      }, 1000)
    },
    upload () {
      let that = this
      if (this.Authorization !== '' && this.formdata !== null) {
        this.setwaitingwz()
        $.ajax({
          url: 'http://13.231.214.207:9000/postdata',
          // url: 'http://localhost:9000/postdata',
          type: 'post',
          data: this.formdata,
          beforeSend: function (request) {
            request.setRequestHeader('Authorization', that.Authorization)
          },
          xhrFields: {
            withCredentials: false
          },
          crossDomain: true,
          processData: false,
          contentType: false,
          success: (res) => {
            clearInterval(this.setInterval)
            that.waiting = '重新开始识别'
            this.isstart = false
            that.result = res.passed
            that.liveness_status = res.liveness_status
          }
        })
      }
    },
    handlerUpload (e) {
      // 获取选定的文件
      let tFiles = e.target.files
      let len = tFiles.length
      if (len > 0) {
        let param = new FormData()
        param.append('video_file', tFiles[0])
        param.append('return_image', true)
        param.append('return_status', true)
        this.formdata = param
        this.upload()
      }
    }
  }
}
</script>

<style scoped>
.circle {
  border-radius: 500px;
  width: 250px;
  height: 250px;
  display: flex;
  background: #219fe8;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 22px;
  margin: auto;
}
</style>



